import React, { useState } from 'react';
import Grid from '@mui/material/Grid';
import AppBar from '@mui/material/AppBar';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { Link } from 'react-router-dom';

const MyProfileView = () => {
  const [nickName, setNickName] = useState('');
  const [psnID, setPsnID] = useState('');
  const [hasTank, setHasTank] = useState(false);
  const [hasHealer, setHasHealer] = useState(false);
  const [hasDD, setHasDD] = useState(false);
  const [hasPvP, setHasPvP] = useState(false);

  const update = () => {
    const data = {
      nick_name: nickName,
      psn_id: psnID,
      has_tank: hasTank,
      has_healer: hasHealer,
      has_dd: hasDD,
      has_pvp: hasPvP
    }
    const req = {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
    };
    fetch('https://b5nwemckb3.execute-api.ap-northeast-1.amazonaws.com/dev/api/profile', req)
      .then(response => response.json())
      .then(data => console.log(data));
  }

  return (
    <div>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            個人資訊
          </Typography>
          <Link to={'/main'}>
            <Button>回首頁</Button>
          </Link>
        </Toolbar>
      </AppBar>
      <Grid container justifyContent={'center'} marginTop={5}>
        <Paper
          elevation={3}
          component={Grid}
          item container
          justifyContent={'center'}
          xs={10} sm={8} md={6}
        >
          <Grid
            item container
            direction="column"
            spacing={2}
            xs={11}
            marginTop={2}
            marginBottom={2}
          >
            <Grid item>
              <TextField
                fullWidth
                required
                id="psn-id-textfield"
                label="PSN ID"
                value={psnID}
                onChange={e => setPsnID(e.target.value)}
              />
            </Grid>
            <Grid item>
              <TextField
                fullWidth
                required
                id="nickname-textfield"
                label="暱稱"
                value={nickName}
                onChange={e => setNickName(e.target.value)}
              />
            </Grid>
            <Grid item container>
              <FormGroup>
                擁有角色：
                <FormControlLabel
                  control={<Checkbox />} 
                  label="坦克" 
                  checked={hasTank} 
                  onChange={e => setHasTank(e.target.checked)}
                />
                <FormControlLabel 
                  control={<Checkbox />} 
                  label="補師" 
                  checked={hasHealer}
                  onChange={e => setHasHealer(e.target.checked)}
                />
                <FormControlLabel 
                  control={<Checkbox />} 
                  label="輸出"
                  checked={hasDD}
                  onChange={e => setHasDD(e.target.checked)}
                />
                <FormControlLabel 
                  control={<Checkbox />} 
                  label="PvP" 
                  checked={hasPvP}
                  onChange={e => setHasPvP(e.target.checked)}
                />
              </FormGroup>
            </Grid>
            <Grid item container justifyContent={'right'}>
              <Button variant="contained" onClick={e => update()}>更新個人資訊</Button>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </div>
  );
};

export default MyProfileView